import React from 'react';
import '../App.css';

export default function Slide(props) {
  //console.log(props)
  return(
    <div className="col-md-6 col-md-4 col-lg-4">
      <div className="team-block bottom">
        <img src={props.imgur} className="img-responsive" alt="img" />
        <div className="team-content">
          <h4>Map {props.tokid}</h4>
          <h6><a href={props.playur}>Play</a></h6>
          <h6><a href={props.detur}>Stats</a></h6>
        </div>
      </div>
    </div>
  )
}
    