import React from 'react';
import { useHistory } from "react-router-dom";
import '../App.css';

export default function Choice(props) {
  const history = useHistory();

  function handleClick() {
    history.push("./profile");
  }
  //console.log(props)
  return(
    <div className="col-md-6 col-md-4 col-lg-4">
      <div className="team-block bottom">
        <img src={props.imgur} className="img-responsive" alt="img" onClick={(e) => {
                        e.preventDefault();
                        localStorage.setItem('avatar',props.imgur)
                        handleClick()
                        //console.log(localStorage.getItem('avatar'))
                      }}/>
        <div className="team-content">
          <h4>{props.slug} #{props.tokid}</h4>
        </div>
      </div>
    </div>
  )
}
    