import React, { useEffect, useState} from "react";

import { useDispatch, useSelector } from "react-redux";
import { connect } from "../redux/blockchain/blockchainActions";
import { fetchData } from "../redux/data/dataActions";


import axios from 'axios';
import Choice from "./Choice";

import * as s from "../styles/globalStyles";
import styled from "styled-components";
//import { create } from "ipfs-http-client";
import mainLogo from './../img/hero-img.png';

export const StyledButton = styled.button`
  font-family: "Roboto", sans-serif;
  text-transform: uppercase;
  font-weight: 400;
  font-size: 13px;
  letter-spacing: 1px;
  display: inline-block;
  padding: 11px 36px;
  border-radius: 50px;
  transition: 0.5s;
  margin-bottom: 30px;
  border: 2px solid #71c55d;

  color: #71c55d;
  text-decoration: none;
`;

export const RadioBtn = styled.button`
  font-family: "Roboto", sans-serif;
  text-transform: uppercase;
  font-weight: 400;
  font-size: 13px;
  letter-spacing: 1px;
  display: inline-block;
  padding: 11px 36px;
  border-radius: 50px;
  transition: 0.5s;
  margin-bottom: 30px;
  border: 2px solid #71c55d;

  color: #71c55d;
  text-decoration: none;
`;



function Avatar() {
  const dispatch = useDispatch();
  const blockchain = useSelector((state) => state.blockchain);
  //const data = useSelector((state) => state.data);
  //const [feedback, setFeedback] = useState("");
  const [checkingNft, setcheckingNft] = useState(false);

  const [collectionTable,setcollectionTable]=useState("");
  let fetching = false



  var arry = []
  //const imgurl = "./maps/" + id2 + ".png";


  function chkArray(arrx){
    var tabletext = arrx
    if (tabletext.length > 3){
      //console.log(fetching)
      /**tabletext.map((item) => {
        console.log(item.tokid)
        
      })*/
      if (fetching == false){
        
        let newData = tabletext.map((item) => {
          //console.log(item)
          return (
            
            <Choice slug={item[0]} tokid={item[1]} imgur={item[2]} />
          )
        })

        
        /*
        let newData = tabletext.map((item) => {
          //console.log(item)
          return (
            
            item + '<br/>'
          )
        }) */
        setcollectionTable(newData)
      }
      
      
    }
    
  }

  

  function chkNFTs  (addr) {
    if (fetching == false) {
      fetching = true
      arry = []
      const options = {method: 'GET'};
      const req1 = 'https://api.opensea.io/api/v1/assets?owner='+addr+'&offset=0&limit=50'
        //console.log(req1)

        axios.get(req1, options)
        .then(res=>{
          //console.log(res.data.assets)
          const lim = res.data.assets.length
          
          //let newface = ""
          for(var i = 0; i < lim; i++) {
                // only display non spam nfts floor price more than 0.009 and collection count more than 99 and owners more than 99
            var obj = res.data.assets[i];
            //console.log()
            //href="https://opensea.io/collection/"+obj.slug
            //console.log(obj.image_url)
            
            var obj = res.data.assets[i];
            let coll = obj.slug
            let tokid = obj.token_id;
            let imgur = obj.image_url
            let newface = [coll, tokid, imgur]
            
            if (i<lim && i > lim-5){
              localStorage.setItem("m"+(lim-i),imgur)
            }
    
            arry.push(newface)
          
          }
          fetching=false
       
          
          //console.log(localStorage.getItem("m5"))
          
          chkArray(arry)
          
          
          
          //arrz = arrz.flat
          //dispatch(arr)
          //console.log(dataZ)
        },[]).then(
            //console.log (arrz)
            
            fetching=false
            
        ) 
      }
    }





    

  const getData = () => {
    if (blockchain.account !== "" && blockchain.smartContract !== null) {
      dispatch(fetchData(blockchain.account));
    }
  };

  useEffect(() => {
    getData();
  }, [blockchain.account]);
  
  useEffect(() => {
    document.title = "MetaMaps";
  }, []);

  return (
    <div>  
      <section id="team" className="padd-section text-center wow fadeInUp">
                <h2>Choose NFT Avatar</h2>

                {blockchain.account === "" ||
                blockchain.smartContract === null ? (
                  <s.Container ai={"center"} jc={"center"}>
                    <s.SpacerSmall />
              
                    <StyledButton className="btn-get-started scrollto"
                      onClick={(e) => {
                        e.preventDefault();
                        dispatch(connect());
                        getData();
                      }}
                    >
                      CONNECT
                    </StyledButton>
                    {blockchain.errorMsg !== "" ? (
                      <>
                        <s.SpacerSmall />
                        <p>{blockchain.errorMsg}</p>
                      </>
                    ) : null}
                  </s.Container>
                ) : (
                  
                  <s.Container ai={"center"} jc={"center"} fd={"row"}>
                    
                        <s.SpacerSmall />
                    <StyledButton className="btn-get-started scrollto"
                      disabled={checkingNft ? 1 : 0}
                      onClick={(e) => {
                        e.preventDefault();
                        getData();
                        chkNFTs(blockchain.account);
                      }}
                     >
                      {checkingNft ? "BUSY" : "Fetch NFTs"}
                     
                    </StyledButton>
                    <p>(Click twice to load)</p>
                    <s.SpacerSmall />
                    <div className="container">
                    <div className="row">
                   
                    {collectionTable}
                   
                    
                      
                      </div></div>
                  </s.Container>
                  
                )}
          </section>
        </div>
  )
}

export default Avatar;
