import React, { useEffect, useState, useRef } from "react";
import { withRouter, useParams } from "react-router-dom";
import axios from 'axios';

import { useDispatch, useSelector } from "react-redux";
import { connect } from "../redux/blockchain/blockchainActions";
import { fetchData } from "../redux/data/dataActions";

import Slide from "./Slide";

import * as s from "../styles/globalStyles";
import styled from "styled-components";
import { NavItem } from "react-bootstrap";
//import { create } from "ipfs-http-client";

export const StyledButton = styled.button`
  font-family: "Roboto", sans-serif;
  text-transform: uppercase;
  font-weight: 400;
  font-size: 13px;
  letter-spacing: 1px;
  display: inline-block;
  padding: 11px 36px;
  border-radius: 50px;
  transition: 0.5s;
  margin-bottom: 30px;
  border: 2px solid #71c55d;

  color: #71c55d;
  text-decoration: none;
`;






export default function Profile() {
  
  let fetching = false

  const dispatch = useDispatch();
  const blockchain = useSelector((state) => state.blockchain);
  //const data = useSelector((state) => state.data);
  const [stateZ,setStateZ]=useState("");
  const [bigTable,setBigTable]=useState("");
  

  useEffect(() => {
    getData();
    //chkNFTs();
  }, [blockchain.account]);
  
  useEffect(() => {
    document.title = "MetaMaps";
  }, []);
  



  const getData = () => {
    if (blockchain.account !== "" && blockchain.smartContract !== null) {
      dispatch(fetchData(blockchain.account));
    }
  };

  var arrz = []
  //const imgurl = "./maps/" + id2 + ".png";


  function chkArray(){
    var tabletext = stateZ
    if (tabletext.length > 3){
      //console.log(fetching)
      /**tabletext.map((item) => {
        console.log(item.tokid)
        
      })*/
      if (fetching == false){
        let newData = tabletext.map((item) => {
          //console.log(item)
          return (
            
            <Slide tokid={item[0]} imgur={item[1]}  playur={item[2]} detur={item[3]}/>
          )
        })
        setBigTable(newData)
      }
      
      
    }
    
  }

  

  const options = {method: 'GET'};
  function chkNFTs  (addr) {
    if (fetching == false) {
      fetching = true
      arrz = []
    const req1 = "https://api.opensea.io/api/v1/assets?owner="+addr+"&asset_contract_address=0x8236afc3447c55a499dc62d62080321563d47342&offset=0&limit=50"
        //console.log(req1)

        axios.get(req1, options)
        .then(res=>{
          //console.log(res.data.assets)
          const lim = res.data.assets.length
          for(var i = 0; i < lim; i++) {
            var obj = res.data.assets[i];
    
            let tokid = obj.token_id;
            let imgur = "https://metamapsnft.com/maps/" + tokid + ".png"
          
          axios.get("https://metamapsnft.com/maps/" + tokid + ".json")
          .then(res2=>{
                let seed = res2.data.dna;
                
                let playur = "https://metamapsnft.com/play/?id="+seed
                let detur = "https://metamapsnft.com/detail/"+tokid
                let newmap = [tokid, imgur, playur, detur]
                
                arrz.push(newmap)
                //res = arr
                fetching = false
            })
            .catch(err=>{
                console.log(err);
            })
          }
          fetching=false
          setStateZ(arrz)
          chkArray()
        },[]).then(
            
            fetching=false
            
        )
      }
    }
    
     
      //...............
    return(
      <>
      <section id="team" className="padd-section text-center wow fadeInUp">
 
            <h2>Choose MetaMap</h2>
  

        {blockchain.account === "" ||
          blockchain.smartContract === null ? (
            <s.Container ai={"center"} jc={"center"}>
                <s.SpacerSmall />
              <StyledButton className="btn-get-started scrollto"
                onClick={(e) => {
                  e.preventDefault();
                  dispatch(connect());                      
                  getData();
                }}
              >
                Connect
              </StyledButton>
              {blockchain.errorMsg !== "" ? (
                <>
                  <s.SpacerSmall />
                    {blockchain.errorMsg}
                </>
              ) : null}
            </s.Container>
          ) : (
            <s.Container ai={"center"} jc={"center"} fd={"row"}>
               <s.SpacerSmall />
              <StyledButton className="btn-get-started scrollto"
                
                onClick={(e) => {
                  e.preventDefault();
                  getData();
                  chkNFTs(blockchain.account)
                  
                }}
              >
                 {fetching ? "BUSY" : "Fetch Maps"}
              </StyledButton>
              <s.SpacerSmall />
              <p>(Click twice to load)</p>
                    <s.SpacerSmall />
              <div className="container">
          <div className="row">
          {bigTable}
            
            </div></div>
             
                      
                    
            </s.Container>
            
          )}
      </section>
    </>
  )
 
  
  
}