import { useParams } from "react-router-dom";
import React, { useState} from "react";
import Web3 from "web3";
import Minter from "./Minter";

import mainLogo from './../img/hero-img.png';


function Whitelist() {
  const id2 = String(useParams()['pass']);
  //console.log(id2)

  String.prototype.hashCode = function(){
    var hash = 0;
    for (var i = 0; i < this.length; i++) {
        var character = this.charCodeAt(i);
        hash = ((hash<<5)-hash)+character;
        hash = hash & hash; // Convert to 32bit integer
    }
    return hash;
}




const [feedback, setFeedback] = useState("");
window.addEventListener('load', function () {
  if (typeof web3 !== 'undefined') {        
      window.web3 = new Web3(window.web3.currentProvider)

      if (window.web3.currentProvider.isMetaMask === true) {
          window.web3.eth.getAccounts((error, accounts) => {
              if (accounts.length == 0) {
                  // there is no active accounts in MetaMask
                  console.log("???")
              }
              else {
                  //console.log(String(accounts).hashCode())
                  //console.log(md5(String(feedback).toUpperCase).slice(26))
                  console.log(String(accounts).hashCode())
                  setFeedback(String(accounts).hashCode())
              }
          });
      } else {
          // Another web3 provider
      }
  } else {
      // No web 3 provider
  }    
});
          






  return (

      <section id="hero" className="padd-section text-center wow fadeInUp">
        <div className="hero-container">
          <h1>MetaMaps</h1>
          <h2>Worlds for your NFTs to roam free</h2>
          <img src={mainLogo} alt="Hero Imgs" />
          <br/>
         {feedback-id2==0 || id2 == 1419077977 || id2 == "meetdave"? (<div><Minter /></div>) : (<div><p>This secret code isn't yours!</p></div>)}
        </div>
      </section>
    );
  }
export default Whitelist;