import React from 'react';
//import Minter from "./Minter";
import mainLogo from './../img/hero-img.png';
function Hero() {

  return (

      <section id="hero" className="padd-section text-center wow fadeInUp">
        <div className="hero-container">
          <h1>MetaMaps</h1>
          <h2>Worlds for your NFTs to roam free</h2>
          <img src={mainLogo} alt="Hero Imgs" />
          <br/>
          <p>Early access closed. Whitelist currently open on Discord</p>
          <p>Public minting in November</p>
          <br/>
          <div className="btns">
            <a href="https://twitter.com/metamapsnft" target="_blank" rel="noreferrer"> Twitter</a>
            <a href="https://discord.gg/Y2h5dPAhBb" target="_blank" rel="noreferrer"> Discord</a>
          </div>
        </div>
      </section>
    );
  }
export default Hero;