import React, { useEffect, useState} from "react";
import Web3 from "web3";

import * as s from "../styles/globalStyles";
import styled from "styled-components";
//import { create } from "ipfs-http-client";
import mainLogo from './../img/hero-img.png';

export const StyledButton = styled.button`
  font-family: "Roboto", sans-serif;
  text-transform: uppercase;
  font-weight: 400;
  font-size: 13px;
  letter-spacing: 1px;
  display: inline-block;
  padding: 11px 36px;
  border-radius: 50px;
  transition: 0.5s;
  margin-bottom: 30px;
  border: 2px solid #71c55d;
  color: #71c55d;
  text-decoration: none;
`;

//import { create } from "ipfs-http-client";


function Early() {
  
String.prototype.hashCode = function(){
    var hash = 0;
    for (var i = 0; i < this.length; i++) {
        var character = this.charCodeAt(i);
        hash = ((hash<<5)-hash)+character;
        hash = hash & hash; // Convert to 32bit integer
    }
    return hash;
}




const [feedback, setFeedback] = useState("");
window.addEventListener('load', function () {
  if (typeof web3 !== 'undefined') {        
      window.web3 = new Web3(window.web3.currentProvider)

      if (window.web3.currentProvider.isMetaMask === true) {
          window.web3.eth.getAccounts((error, accounts) => {
              if (accounts.length == 0) {
                  // there is no active accounts in MetaMask
              
              }
              else {
                  //console.log(String(accounts).hashCode())
                  //console.log(md5(String(feedback).toUpperCase).slice(26))
               
                  setFeedback(String(accounts).hashCode())
              }
          });
      } else {
          // Another web3 provider
      }
  } else {
      // No web 3 provider
  }    
});
          


useEffect(() => {
  document.title = "MetaMaps";
}, []);


return (
  <div>  

      <section id="faq" className="padd-section text-center wow fadeInUp">
        <div className="hero-container">
          <br/>
          <h2>Whitelist - Early Mint</h2>
          <img src={mainLogo} alt="Hero Imgs" />
          <br/> <br/>
          {feedback === "" ? (
                <s.Container ai={"center"} jc={"center"}>
                 
                  <p>Connect to MetaMask to get your mint code</p>
                  <br/>
                  
                </s.Container>
              ) : (
                <s.Container ai={"center"} jc={"center"} fd={"row"}>
                  <p>
                  <a href={"./mint/" + feedback}>Secret URL</a>
                  </p>
                   
                </s.Container>
                
              )}

<a href="./early">Refresh</a>
        </div>
      </section>
              
        
      </div>
)
}

export default Early;
