import React from 'react';
import cloud from "./../img/svg/1.gif";
import planet from "./../img/svg/2.png";
import astroid from "./../img/svg/3.png"
import banner from "./../img/newDemo.png"
function Intro() {

    return(
    <>
    <section id="get-started" className="padd-section text-center wow fadeInUp">
        <div className="container">
          <div className="section-title text-center">
            <h2>What are MetaMaps?</h2>
            <img src={banner} alt="Banner Imgs" />
            <br/>  <br/>
            <p>MetaMaps is the first ever NFT collection of 10,000 unique and algorithmically generated map terrains that exist on the Ethereum blockchain. Each map will be a place where different NFT PFP collections could gather for their new adventures. For the first time, PFP NFTs don't have to just be your twitter profile pics. Building and exploring cross-collection adventure maps.
            </p>
           
            <p> Our MetaMap generator randomizes the composition of terrains to produce time-based 1/1 unique maps, each ready to be built into a new adventure and each evolving over time. As soon as a new map is drawn, you'd want to start exploring.
              Some maps are rarer than others (with purple gems), but they are all unique. There are a few maps that are very special, the ultra-rares. If you are fortunate enough to mint one of these maps, you better hold onto the riches. </p>
            <p />
          </div>
        </div>
        <div className="container">
          <div className="row">
            <div className="col-md-6 col-lg-4">
              <div className="feature-block">
                
                <img src={cloud} alt="img" className="img-fluid" />
                <h4>Self evolving</h4>
                <p>Uniquely randomized weather and events change each map, with rich features decided upon by token holders (explorers)</p>
              </div>
            </div>
            <div className="col-md-6 col-lg-4">
              <div className="feature-block">
                <img src={planet} alt="img" className="img-fluid" />
                <h4>Interoperability</h4>
                <p>NFT characters and items from other projects would be compatible with MetaMap, as well as derivative projects</p>
              </div>
            </div>
            <div className="col-md-6 col-lg-4">
              <div className="feature-block">
                <img src={astroid} alt="img" className="img-fluid" />
                <h4>Full ownership</h4>
                <p>Owners have full rights to use the map terrain data, including building other derivative worlds</p>
              </div>
            </div>
          </div>
        </div>
      </section>      
    </>
    )}
    
export default Intro;